x-sign {
  --interval: 1s;
  display: block;
  text-shadow:
          0 0 10px purple,
          0 0 20px purple,
          0 0 40px red,
          0 0 80px blue;
  will-change: filter, color;
  filter: saturate(60%);
  animation: flicker steps(100) var(--interval) 1s infinite;
}

@keyframes flicker {
  50% {
    color: white;
    filter: saturate(200%) hue-rotate(20deg);
  }
}



