/* Some positioning and ratios */
.sticky-container {
  position: relative;
}

.sticky-outer {
  display: flex;
  /*padding-top: 92.5925926%;*/
  position: relative;

  width: 100%;
}

.sticky {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/* Shadow behind the sticky note */
.sticky:before {
  box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  background-color: rgba(0, 0, 0, 0.25);
  content: '';
  width: 90%;
  left: 5px;
  height: 75%;
  position: absolute;
  top: 30%;

  animation-name: sticky-shadow-animation;
  animation-duration: 1000ms;
  animation-delay: 0ms; /* add this */
  /*animation-timing-function: ease-in;*/
  animation-iteration-count: infinite;
}

@keyframes sticky-shadow-animation {
  0% {
    top : 30%;
    box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  }
  50% {
    top : 29%;
    box-shadow: -2px 2px 16px 0 rgba(0, 0, 0, 0.5);
  }
  100% {
    top : 30%;
    box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  }
}

/* The sticky note itself */
.sticky-content {
  background: linear-gradient(
          180deg,
          rgba(187, 235, 255, 1) 0%,
          rgba(187, 235, 255, 1) 12%,
          rgba(170, 220, 241, 1) 75%,
          rgba(195, 229, 244, 1) 100%
  );
  width: 100%;
  height: 100%;


  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Kalam', cursive;
  font-size: 1.25rem;

  clip-path: url(#stickyClip);
}

